@import url("../../Main.scss");

.muscle_group {
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.219);
  min-width: fit-content;
  margin: 5px;
  border-radius: 12px;
  padding: 5px 15px;
  background-color: white;
  background: radial-gradient(
    circle,
    rgba(206, 202, 202, 0.925) 39%,
    rgba(248, 248, 248, 0.644) 95%
  );
  font-family: "Gill Sans", "Gill Sans MT", "Trebuchet MS", sans-serif;
  h3 {
    margin: 15px 0 5px;
    background-color: black;
    background-clip: text;
    color: transparent;
    text-shadow: rgba(185, 183, 183, 0.5) 0px 3px 3px;
  }

  ul {
    margin: 10px 30px;
    .exercise {
      word-spacing: 2px;
      // line-height: 13px;
      list-style: none;
      font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
        "Lucida Sans", Arial, sans-serif;
      label {
        vertical-align: middle;
        line-height: 22px;
        display: flex;
        .exercise__checkbox {
          display: inline-block;
          align-self: center;
          margin-right: 5px;
        }
        span {
          display: inline-block;
          padding-top: 3px;
          align-self: center;
        }
      }
    }
  }
}
